import { Form, Input } from "antd";

const CInputPassword = ({
  label,
  name,
  value,
  id,
  onChange,
  addonBefore,
  require,
  isDisabled,
  validate,
  ...rest
}) => {
  return (
    <>
      <Form.Item
        label={
          <span>
            {label}
            {require ? (
              <span style={{ marginLeft: "5px", color: "red" }}>*</span>
            ) : null}
          </span>
        }
        name={name}
        {...rest}
        hasFeedback
        validateStatus={validate ? "error" : null}
        help={validate}
      >
        <Input.Password
          addonBefore={addonBefore}
          value={value}
          id={id}
          allowClear
          disabled={isDisabled}
          placeholder={label}
          onChange={onChange}
        />
      </Form.Item>
    </>
  );
};

export default CInputPassword;
